import * as React from 'react';
import { navigate } from 'gatsby';
import { Layout } from '@/components/layout';
import { Seo } from '@/components/seo';

const IndexPage = () => {
  const [opacity, setOpacity] = React.useState(0);

  React.useEffect(() => {
    const link = document.createElement('link');
    link.rel = 'stylesheet';
    link.href = '/css/tutorial.css';

    const script = document.createElement('script');
    script.src =
      'https://cdnjs.cloudflare.com/ajax/libs/rellax/1.12.1/rellax.min.js';
    script.onload = () => {
      setTimeout(() => {
        setOpacity(1);

        setTimeout(() => {
          const Rellax = window.Rellax;

          new Rellax('.rellax-10', { speed: -10 });
          new Rellax('.rellax-9', { speed: -9 });
          new Rellax('.rellax-8', { speed: -8 });
          new Rellax('.rellax-7', { speed: -7 });
          new Rellax('.rellax-6', { speed: -6 });
          new Rellax('.rellax-5', { speed: -5 });
          new Rellax('.rellax-4', { speed: -4 });
          new Rellax('.rellax-3', { speed: -3 });
          new Rellax('.rellax-2', { speed: -2 });
          new Rellax('.rellax-1', { speed: -1 });
        }, 500);
      }, 500);
    };

    document.head.appendChild(link);
    document.body.appendChild(script);

    return () => {
      document.head.removeChild(link);
      document.body.removeChild(script);
    };
  }, []);

  return (
    <Layout>
      <div
        className='container'
        style={{
          transition: 'opacity 1000ms ease-out',
          opacity
        }}
      >
        <div className='parallax-container'>
          {/* 富士山の向こうにある雲 */}
          <div className='parallax-parts kumo-1-2 rellax-10'></div>
          <div className='parallax-parts kumo-1-1 rellax-10'></div>

          {/* 富士山 */}
          <div className='parallax-parts haikei-huji rellax-10'></div>

          {/* 富士山の手前にある雲 */}
          <div className='parallax-parts kumo-2-2 rellax-9'></div>
          <div className='parallax-parts kumo-2-1 rellax-8'></div>

          {/* 山 */}
          <div className='parallax-parts background-haikei rellax-8'></div>
          <div className='parallax-parts background-tyukei rellax-7'></div>
          <div className='parallax-parts background-zenkei rellax-6'></div>

          {/* 森 */}
          <div className='parallax-parts forest rellax-5'></div>

          {/* 犬のエリア */}
          <div className='parallax-parts iwayama-zenkei rellax-inu rellax-4'></div>
          <div className='parallax-parts inu-zenkei rellax-inu rellax-4'></div>

          {/* ロゴ */}
          <div className='kotomusubi-logo'></div>
        </div>

        {/* 雲 */}
        <div className='parallax-parts genjigumo rellax-2'></div>

        <div className='textarea'>
          <section className='first-text'>
            <h2>
              立派な狛犬となることを志した「たま」が
              <br />
              日本古来の言葉である”大和言葉”を人々に伝えるために
              <br />
              懸命に走るタイピングゲームです。
            </h2>
          </section>

          <section className='second-text'>
            <h1>ルール説明</h1>

            <div className='capture'></div>

            <div className='rule-text'>
              <h1>其の一</h1>
              <h2>
                黒い枠の「開始ボタン」でゲームがスタートします。
                <br />
                押した瞬間に始まります。
                <br />
                そのままキーボードで文字を入力してください。
              </h2>
            </div>

            <div className='rule-text'>
              <h1>其の二</h1>
              <h2>
                開始すると画面上部に文章が表示されるので、
                <br />
                赤い枠内に同じ文章を入力してください。
                <br />
                キーボードのエンターキーを押すと、次の文章が表示されます。
              </h2>
            </div>

            <div className='rule-text'>
              <h1>其の三</h1>
              <h2>
                70秒の制限時間があります。時間内にクリアすると、
                <br />
                ランキング登録もでき、上位１０人の名前が表示されます。
                <br />
                頑張ってください。
              </h2>
            </div>

            <a
              href='/play'
              style={{ display: 'inline-block' }}
              onClick={(e) => {
                e.preventDefault();
                navigate('/play');
              }}
            >
              <div
                className='button'
                style={{
                  margin: '0 auto'
                }}
              >
                <h1>挑戦する</h1>
              </div>
            </a>
          </section>
        </div>

        <footer className='footer'>
          <p>
            <small>&copy; 2020 K Shindo</small>
          </p>
        </footer>
      </div>
    </Layout>
  );
};

export default IndexPage;

export const Head = () => {
  return <Seo />;
};
